.dark-mode-gamestore {

	.wp-block-blocks-gamestore-block-header .inner-header .wp-block-navigation .wp-block-navigation__container .wp-block-navigation-item.open-on-click .wp-block-navigation-submenu__toggle + .wp-block-navigation__submenu-icon{
	  background: url('data:image/svg+xml,<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8.47021 10.7402L12.0002 14.2602L15.5302 10.7402" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" /></svg>');
	}  
}

  @media (max-width: 600px){

	html #wp-admin-bar{
		z-index: 100001;
	}

	.wp-block-blocks-gamestore-block-header .wp-block-navigation__responsive-container.is-menu-open{
		background-color: transparent!important;
		background-image: url('img/mobile.png')!important;
        background-size: cover;
		background-repeat: no-repeat;
		overflow: hidden;

		.wp-block-navigation__responsive-container-close{
			top: 24px;
		}

		.wp-block-navigation_responsive-container-content{
			height: calc(100vh - (4rem + 24px));
			padding-top: calc(4rem + 24px); ;
			overflow: auto;
			scrollbar-width: none;
			-ms-overflow-style: none;
		
			&::-webkit-scrollbar {
				display: none; 
				}
			}
	}



	.wp-block-navigation-item__content, .wp-block-navigation__responsive-container-close{
		color: var(--mobile-menu)!important;
	}   


	.wp-block-blocks-gamestore-block-header .inner-header{

		padding-left: 16px;
		padding-right: 16px;
		gap: 8px;

		.wp-block-group{
			width: calc(100% - 168px);
			display: flex!important;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;

			.wp-block-navigation__responsive-container-open {
				width: 36px;
				height: 36px;
				display: block;
				background: url('data:image/svg+xml,<svg width="24" height="25" viewBox="0 0 24 25" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M21 8.0459H3C2.59 8.0459 2.25 7.7059 2.25 7.2959C2.25 6.8859 2.59 6.5459 3 6.5459H21C21.41 6.5459 21.75 6.8859 21.75 7.2959C21.75 7.7059 21.41 8.0459 21 8.0459Z" fill="%230E0D0F" fill-opacity="0.64" /><path d="M21 13.0459H3C2.59 13.0459 2.25 12.7059 2.25 12.2959C2.25 11.8859 2.59 11.5459 3 11.5459H21C21.41 11.5459 21.75 11.8859 21.75 12.2959C21.75 12.7059 21.41 13.0459 21 13.0459Z" fill="%230E0D0F" fill-opacity="0.64" /><path d="M21 18.0459H3C2.59 18.0459 2.25 17.7059 2.25 17.2959C2.25 16.8859 2.59 16.5459 3 16.5459H21C21.41 16.5459 21.75 16.8859 21.75 17.2959C21.75 17.7059 21.41 18.0459 21 18.0459Z" fill="%230E0D0F" fill-opacity="0.64" /></svg>');
			    background-position:center center;
				background-repeat: no-repeat;
				svg{
				display: none!important;
			 }
			}

			.wp-block-site-logo{
				max-width: 140px;
				z-index: 100001;
			}
			.wp-block-navigation{

				margin-top: 0;

			}
	}
	.right-section{
		gap: 8px;

		> div{
			line-height: 0;
		}
	}
}

